$primary-color: #00b494;
$border-color: #E5E6E7;
$font: 'Vazir', sans-serif;

@mixin screen-sm {
    @media screen and (max-width: 800px) {
      @content;
    }
}

.colored-label {
    font-size: 0.7em;
    padding: 2px 10px;
    color: white;
    border-radius: 5px;
}

.red-label {
    background: #EF5463;
}

.yellow-label {
    background: #F9AC51;
}

.blue-label {
    background: #0AC6C9;
}