$primary-color: #00b494;
$border-color: #E5E6E7;
$font: 'Vazir', sans-serif;

@mixin screen-sm {
    @media screen and (max-width: 800px) {
      @content;
    }
}

.compose-email {
    background-color: $primary-color;
    width: 100%;
    color: white;
    padding: 8px;
    border: 1px;
    border-radius: 4px;
}