$primary-color: #00b494;
$border-color: #E5E6E7;
$font: 'Vazir', sans-serif;

@mixin screen-sm {
    @media screen and (max-width: 800px) {
      @content;
    }
}

body {
    margin: 0;
}

.content-wrapper {
    margin-left: 3rem;
    transition: margin-left 2s;
}

.content-wrapper-slide {
    margin-left: 8rem;
}

.content-area {
    display: grid;
    grid-template-columns: 25% 75%;
    font-family: $font;
    background-color: #f3f3f4;
    width: 100%;

    @include screen-sm {
        grid-template-columns: 100%;
    }
}